class List extends React.Component {
      constructor(props) {
        super(props)
        //: Bind new functions for next and previous
          this.state = {
            items: [],
           }
          this.updateUpcomingExams = this.updateUpcomingExams.bind(this)
      }
    updateUpcomingExams = (results) => {
        console.log(results)
        this.setState({items: results})
        this.forceUpdate()
    }
    
    componentDidMount() {
      Functions.upcomingExams(this.updateUpcomingExams)
     }
    
    render() {
        let items = this.state.items
        console.log(items)
//        console.log(this.props.exams)
        return(
               <ul>
                 {
               items.map((item, i) => <ListItem key={i} value={item}/>)
                 }
               </ul>
        )
    }
    
}
               
