function setAnswer(answer) {
    return (previousState, currentProps) => {
      return { ...previousState,
                    answer: answer,
                };
    };
}

function setQuestion(question) {
    return (previousState, currentProps) => {
      return { ...previousState,
                    question: question,
                };
    };
}

function setSubject(subjectId) {
    return (previousState, currentProps) => {
      return { ...previousState,
                    subjectId: subjectId,
                };
    };
}

