import Functions from './functions'
 
function setQuiz(quizzes) {
    return (previousState, currentProps) => {
      return { ...previousState,
                    quizzes: quizzes,
                };
    };
}

function setParams(id, value) {
    return (previousState, currentProps) => {
        if (id == 'termCount'){
            return { ...previousState,
                    termCount: value,
            };
        }else if (id == 'examType'){
            return { ...previousState,
                    examType: value,
            };
        }else if (id == 'subject'){
            return { ...previousState,
                    subject: value,
            };
        }else if (id == 'image'){
            return { ...previousState,
                    image: value,
            };
    }
    }
}

function setQuestion(id, value) {
    console.log("id: " + id + " | value: " + value)
    return (previousState, currentProps) => {
        if (id == 'Question'){
            return { ...previousState,
                    question: value,
            };
        }else if (id == 'questionType'){
            return { ...previousState,
                    questionType: value,
            };
        }else if (id == 'Answer'){
            return { ...previousState,
                    answer: value,
            };
        }else if (id == 'image'){
            return { ...previousState,
                    image: value,
            };
        }else if( id=='AnswerA'){
            return { ...previousState,
                          answerA: value,
                  };
        }else if( id=='AnswerB'){
            return { ...previousState,
                          answerB: value,
                  };
        }else if( id=='AnswerC'){
            return { ...previousState,
                          answerC: value,
                  };
        }else if( id=='AnswerD'){
            return { ...previousState,
                          answerD: value,
                  };
        }
    }
}

function setQuestions(questions) {
    console.log("questions: " + questions)
    return (previousState, currentProps) => {
        return { ...previousState,
            questions: questions,
        }
    }
}


function emptyQuestion() {
//    console.log("questions: " + questions)
    return (previousState, currentProps) => {
            return { ...previousState,
                    question: '',
                    questionType: '',
                    answer: '',
                    image: '',
                    answerA: '',
                    answerB: '',
                    answerC: '',
                    answerD: '',
            };
    }
}

