var hasToken = false;

function saveQuestion(updateSaveQuestion, question, description, date, subjectId){
      fetch('http://'+url+'/admin/saveQuestion', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          DATE: date,
          QUESTION: question,
          ANSWER: description,
          SUBJECT_ID: subjectId,
        })
            }).then((response) => response.json())
            .then((responseJson) => {
        //Showing response message coming from server after inserting records.
          console.log(responseJson);
        if (typeof responseJson.affectedRows != "undefined") {
            updateSaveQuestion(responseJson.affectedRows);
        }
        }).catch((error) => {
          console.error(error);
        });
}

function saveQuiz(examType, subject, termCount, image, subjectId, questions) {
    //Functions.saveQuiz(this.state.examType, this.state.subject, this.state.termCount, this.state.image, this.state.questions)
      fetch('http://'+url+'/admin/saveQuiz', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          SUBJECT: subject,
          TERM_COUNT: termCount,
          EXAM_TYPE: examType,
          IMAGE: image,
          SUBJECT_ID: subjectId,
          QUESTION: questions
        })
        }).then((response) => response.json())
        .then((responseJson) => {
    //Showing response message coming from server after inserting records.
          console.log(responseJson);
    //if (typeof responseJson.affectedRows != "undefined") {
    //updateSaveQuestion(responseJson.affectedRows);
    //}
        }).catch((error) => {
          console.error(error);
        });
}

function upcomingExams(updateUpcomingExams) {
       fetch('http://'+url+'/home/upcomingExams', {
          method: 'GET',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
          }
        }).then((response) => response.json())
        .then((responseJson) => {
              if (responseJson.length){
            updateUpcomingExams(responseJson)
              }
        }).catch((error) => {
            console.error(error);
        });
}

