function login(updateLogin, username, password) {
    //Functions.saveQuiz(this.state.examType, this.state.subject, this.state.termCount, this.state.image, this.state.questions)
      fetch('http://'+url+'/auth/login', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          PHONE_NUMBER: username,
          PASSWORD: password,
        })
        }).then((response) => response.json())
        .then((responseJson) => {
    //Showing response message coming from server after inserting records.
          console.log(responseJson);
              if (typeof responseJson.results != "undefined") {
                updateLogin(responseJson.results);
              }
        }).catch((error) => {
          console.error(error);
        });
}

